.user{
    box-shadow: 10px 8px 15px -2px rgba(135,135,135,0.37);
    background-color: rgb(0, 255, 8);
    margin-bottom:30px;
    width:600px;
    padding:20px;
    }
    .user h2,.user h3{
        color:rgba(0,0,0,0.541);
    }
    .user span
    {
    font-weight:500;
    color:black;
    }
    .user__bottom
    {
    margin-top:20px;
    }
    .user__delete
    {
    border:none;
    outline:none;
    padding:10px;
    width:100px;
    background-color:rbga(255,0,0,0.818);
    border-radius:8px;
    color:rgb(35, 5, 5);
    cursor:pointer;
    }
    .user_empty{
        background-image: url(https://th.bing.com/th/id/R.d56775d52a34c68b11f21e4b40dc218a?rik=BRvvLt9eho5EVQ&riu=http%3a%2f%2fwww.pixelstalk.net%2fwp-content%2fuploads%2f2016%2f07%2fFree-Plain-HD-Photos.jpg&ehk=dAdJlAd2pfBqxq%2b%2bPAT5qAvfCPpipC9%2fv3uI68BLGNs%3d&risl=&pid=ImgRaw&r=0);
        background-size: cover;
        background-repeat: no-repeat;
    }
    p{
        color:blue;
    }