*
{
    margin:0;
    padding:0;
}
.home
{
    
    max-width:2000px;
    margin: auto;
    image-resolution:inherit;
    background-image: url( https://i.ytimg.com/vi/wQ5Wc1_PeD0/maxresdefault.jpg);
    background-position-y: top;
    
   
}
 
.home__container
{
    
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    
}
.home__formContainer {
    box-shadow: 10px 10px 5px 0px rgba(149,62,202,0.75);
-webkit-box-shadow: 10px 10px 5px 0px rgba(149,62,202,0.75);
-moz-box-shadow: 10px 10px 5px 0px rgba(149,62,202,0.75);
    background:#08a515;
    padding:10px;
    border-radius:5px;
    width:290px;
    margin-left:50%;
    margin-top: -40%;
    

}
.home__formContainer input{
    border:1px solid #e5e5e5;
    box-shadow: 10px 10px 5px 0px rgba(149,62,202,0.75);
    outline:none;
    padding:5px 10px;
    width:80%;
    border-radius:5px;
    margin:10px 0;
    background:transparent;
    color:white;
    font-size:14px;
    margin-top: -40%;
}
.home__formContainer input::placeholder
{
    color:white;
}
.home__formContainer button{
    background: transparent;
border:1px solid white;
outline:none;
font-size:18px;
color:white;
padding:10px;
width:150px;
margin-top:20px;
border-radius:12px;
cursor:pointer;
}
.home_title
{
  color:rgb(193, 180, 205);
  text-shadow:#26a40000;

}
button{
    margin-top: -50px;
}
img{
    margin-left: 0 auto;
}