*
{
    margin: 0;
    padding: 0;
}
nav
{
  background-color: rgb(0, 255, 255);
  display: flex;
  padding: 70px;
  align-items: center;
  justify-content: space-between;
  color: azure;
}
nav h1{
    font-size: 50px;
    font-weight:600;
    color: rgb(35, 10, 199);
     text-shadow: 4px 4px 2px rgba(172, 160, 21, 0.6);;
    
}
nav h1:hover{
    color:rgb(176, 176, 208);
}
nav ul
{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width:30%;
    text-shadow:#0008a400;
    
}
nav ul a
{
    text-decoration: none;
    font-size: 20px;
    font-weight: 500;
    color:brown;
}
nav ul a:hover
{
    color:rgb(247, 0, 255);
}
